@import url('https://fonts.googleapis.com/css?family=Merriweather&display=swap');
*{
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  font-family: 'Merriweather', serif;
}

header{
  color: #8ee4af;
}

body{
  background-color: rgb(47, 49, 48);
  color: #edf5e1;
}

nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 8vh;
  background-color: #05386B;
  height: 128px;
  box-shadow: 0px 5px 10px rgb(26, 26, 31);

}

footer{
  box-shadow: 0px -5px 10px rgb(36, 92, 79);
  background-color: #379683;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  color: #edf5e1;
}

nav li{
  color:#8ee4af;
  text-decoration: none;
  letter-spacing: 2px;
  font-size: 16px;
  font-weight: bold;
}

body a{
  text-decoration: none;

}

.logo{
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 22px;
}

.nav-link{
  display: flex;
  justify-content: space-around;
  width: 15%;
}

.nav-link li{
  list-style: none;
}

@media screen and (max-width:1024px){
  .nav-link{
    justify-content: space-around;
    width: 15%;
  }
}

@media screen and (max-width:1024px){
  .nav-link{
    display: inline;
  }
}

.container{
  background-color: rgb(87, 202, 139);
  box-shadow: -10px 10px 10px rgb(35, 80, 55) inset, 10px -10px 10px rgb(132, 228, 175) inset;
  border-radius: 4px;
  width: 85%;
  margin: auto;
  margin-top: 20px;
  text-align: center;
  padding: 25px;
  line-height: 30px;
  color: #05386B;
  font-weight: 600;
}

.title{
  margin-top: 16px;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  line-height: 50px;
  text-shadow: -3px 3px #425c58;
}

.pixel{
  margin-top: 32px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 64px;
  image-rendering: pixelated;
}

.link-img{
  image-rendering: auto;
  width: 48px;
  margin-top: 16px;
  margin-bottom: 16px;
  margin-right: 8px;
  margin-left: 8px;
}